.photo {
  background-size: contain;
  height: 220px;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 100ms ease-in-out;
}

.photo:hover{
  transform: scale(1.05);
}

.photo img{
  max-width: 100%;
}

